import React, { memo } from 'react'
import * as Space from "react-spaces"
import Flex from 'react-flexview'
import Box from '@material-ui/core/Box'
import { Navbar } from 'react-bootstrap'
import styled from "styled-components"
import { useSelector, useDispatch } from '@ttungbmt/redux-toolkit'
import { selectors as layout_sels, actions as layout_acts, utils } from '@core/reducers/layout'
import { Slot } from "@ttungbmt/redux-layout"

// import { isSlot, filterSlot } from '@core/components/Slot'

const HeaderWrapper = styled(Navbar)`
     header { 
        border-bottom: 1px solid #dfdfdf; 
     }
    .header-title {
        font-size: 17px;
        font-weight: 700;
        text-transform: uppercase;
        color: rgb(0, 147, 221);
    }
    .nav-link.active {
        border-bottom: 2px solid #2196f3;
    }
    
    .navbar-toggler { display: block }
    .app-menu {
        a {
            text-transform : uppercase; font-weight: 600
            &:hover {
                color: #2196f3!important;
                background-color: hsla(0,0%,93.7%,.73);
            }
        }
    }
`;


function Header({children, homeUrl = '/'}) {
    const dispatch = useDispatch()
    const header = useSelector(layout_sels.getHeader)
    let background = 'url(https://maps.hcmgis.vn/core/themes/maps/assets/img/bando_02.png) no-repeat 150px 0 #f9f9f9'

    return (
        <HeaderWrapper>
            <Space.Top as="header" {...utils.pickSpace(header, ['size'])} style={{background, overflow: 'visible'}}>
                <Space.Left size={450}>
                    <Flex height={'100%'}>
                        <Flex grow>
                            <a href={homeUrl}>
                                <Box display="flex" alignItems="center" height="100%">
                                    <Box m={'0 7px'}>
                                        <Slot name="header.logo"/>
                                    </Box>
                                    <Box className="header-title">
                                        <Slot name="header.title"/>
                                    </Box>
                                </Box>
                            </a>
                        </Flex>
                        <Flex>
                            <button type="button" className="navbar-toggler" onClick={() => dispatch(layout_acts.toggleSidebar())}>
                                <i className="icon-paragraph-justify3"/>
                            </button>
                        </Flex>
                    </Flex>
                </Space.Left>
                <Space.Fill style={{overflow: 'visible'}}>
                    <Box display="flex" height="100%">
                        <Flex grow>
                            <Slot name="header.search_bar"/>
                        </Flex>
                        <Box display="flex" alignItems="center" className="app-menu">
                            <Slot name="header.menu"/>
                        </Box>
                    </Box>
                </Space.Fill>
            </Space.Top>
        </HeaderWrapper>
    )
}

export default memo(Header)