import { useSelector, useDispatch } from '@ttungbmt/redux-toolkit'
import { selectors as layout_sels, actions as layout_acts } from './index'
import { useFullscreen as baseUseFullscreen } from '@ttungbmt/react-hooks'

export const useFullscreen = ({ref, onClose}) => {
    const dispatch = useDispatch()
    const show = useSelector(layout_sels.getAppFullscreen)

    baseUseFullscreen(ref, show, {
        onClose: () =>  {
            onClose ? onClose() : dispatch(layout_acts.toggleAppFullscreen())
        }
    })

    return [show]
}
