import { routerMiddleware } from 'connected-react-router'
import createReducer from './reducers'
import { configureStore as baseConfigureStore, getDefaultMiddleware, createSagaMiddleware  } from '@ttungbmt/redux-toolkit'
import { loadingBarMiddleware } from 'react-redux-loading-bar'

export default function configureStore(initialState = {}, injectedReducers = {}, history) {
    const reduxSagaMonitorOptions = {};

    const sagaMiddleware = createSagaMiddleware(reduxSagaMonitorOptions);

    const store = baseConfigureStore({
        reducer: createReducer(),
        preloadedState: initialState,
        middleware: [
            ...getDefaultMiddleware(),
            sagaMiddleware,
            routerMiddleware(history),
            loadingBarMiddleware({
                promiseTypeSuffixes: ['REQUEST', 'SUCCESS', 'FAILURE'],
            })
        ],
    })



    // Extensions
    store.runSaga = sagaMiddleware.run;
    store.injectedReducers = {}; // Reducer registry
    store.injectedSagas = {}; // Saga registry

    // Make reducers hot reloadable
    if (process.env.NODE_ENV === 'development' && module.hot) {
        module.hot.accept('./reducers', () => {
            const newRootReducer = createReducer(store.injectedReducers)
            store.replaceReducer(newRootReducer)
        })
    }

    return store;
}