import { map } from 'lodash-es'
import qs from 'qs'

export function isTrue(str) {
    return str === 'true' || str === true
}

export function getParamUrl(param) {
    const url = new URL(window.location.href)
    return url.searchParams.get(param)
}

export const generateRoutesFromConfigs = (configs) => {
    let allRoutes = [];

    map(configs, (config) => {
        allRoutes = [
            ...allRoutes,
            ...setRoutes(config)
        ]
    })

    return allRoutes;
}

export const getParsedQuerySettings = () => {
    const parsedQueryString = qs.parse(window.location.search, {ignoreQueryPrefix: true});
    if (parsedQueryString && parsedQueryString.defaultSettings) {
        return JSON.parse(parsedQueryString.defaultSettings);
    }
    return {}
    // Generating route params from settings
    /*const settings = qs.stringify({
        defaultSettings: JSON.stringify(defaultSettings, {strictNullHandling: true})
    });
    console.info(settings);*/
}

const setRoutes = (config) => {
    let routes = [...config.routes];
    if (config.settings || config.auth) {
        routes = routes.map((route) => {
            let auth = config.auth ? [...config.auth] : [];
            auth = route.auth ? [...auth, ...route.auth] : auth;
            return {
                ...route,
                settings: {...config.settings, ...route.settings},
                auth
            };
        });
    }

    return [...routes];
}