import { uniqid, includes } from '@ttungbmt/utils'
import * as acts from './cabenh.actions'

const cabenhSlice = {
    caseReducers: {
        [acts.createCabenh.type]: ({payload}, Cabenh) => {
            Cabenh.create({...payload, id: uniqid()})
        },
        [acts.createMany.type]: ({payload}, Cabenh) => {
            payload.filter(p => p.geometry).map(p => Cabenh.create({...p, id: uniqid()}))
        },
        [acts.selected.type]: ({payload: id}, Cabenh) => {
            Cabenh.all().update({selected: false})
            Cabenh.withId(id).update({selected: true})
        },
        [acts.removeAll.type]: ({payload}, Cabenh) => {
            Cabenh.all().delete();
        },
        [acts.deleteCabenhById.type]: ({payload: id}, Cabenh) => {
            Cabenh.filter({gid: id}).delete()
        },
        [acts.remove.type]: ({payload: ids}, Cabenh) => {
            Cabenh.filter(c => includes(ids, c.gid)).delete()
        },
    }
}

export default cabenhSlice