import React, { createElement, memo, useContext, useEffect, useCallback } from 'react'

import PropTypes from 'prop-types'
import { isPlainObject, isArray, get, isEqual } from 'lodash-es'
import { LayoutContext } from "./LayoutProvider"

export const getTemplates = (parent) => {
    if (isPlainObject(parent)) {
        // Check if the element is a template
        if (parent.type && isEqual(get(parent, 'type.type.displayName'), 'Template')) {
            return {[parent.props.slot]: parent};
        }
        return {};
    } else if (isArray(parent)) {
        let templates = [];
        for (let i = 0, c = parent.length; i < c; i += 1) {
            templates = Object.assign({}, templates, getTemplates(parent[i]));
        }
        return templates;
    }
    return {};
}

function BaseLayout({children, layout: name, ...props}) {
    const context = useContext(LayoutContext)

    const getLayout = useCallback((name) => {
        const layout = context.getLayout(name)
        return createElement(layout, props, children)
    }, [name])

    const layout = getLayout(name, props, children)
    const templates = getTemplates(children)

    if (!layout) throw new Error(`No layout found named: '${name}'`)


    useEffect(() => {
        context.setLayout({name, templates})
    }, [name])

    return layout
}

BaseLayout.propTypes = {
    layout: PropTypes.string.isRequired,
}

export default memo(BaseLayout)