import { createSlice } from '@ttungbmt/redux-toolkit'

export const KEY = 'pcd'

let initialState = {
    odich: {
        radius: 200,
        combineCir: true
    },
    khoanhvung: []
}

const reducers = {
    setCombineCir: (state, {payload}) => {
        state.odich.combineCir = payload
    },
    khoanhVung: (state, {payload}) => {
        state.khoanhvung.push(payload)
    },
    removeAllKhoanhVung: (state, {payload}) => {
        state.khoanhvung = []
    },
}

const odichSlice = createSlice({
    name: KEY,
    initialState,
    reducers,
})


export default odichSlice