import React, { memo } from 'react'
import { isBoolean } from 'lodash-es'
import BaseBox from '@material-ui/core/Box';

function Box(props) {
    const {flex, row, column, wrap, grow, shrink, bold, regular, h100, style = {}, uppercase, relative,
        absolute, ...outter} = props
    let inner = {style}

    if (flex) inner.display = 'flex'
    if (row) inner.flexDirection = 'row'
    if (column) inner.flexDirection = 'column'
    if (wrap) inner.flexWrap = 'wrap'
    if (shrink) inner.flexShrink = isBoolean(shrink) ? 1 : shrink
    if (grow) inner.flexGrow = isBoolean(grow) ? 1 : grow
    if (regular) inner.fontWeight = 'fontWeightRegular'
    if (bold) inner.fontWeight = 500
    if (h100) inner.style.height = '100%'
    if (uppercase) inner.style.textTransform = 'uppercase'
    if (relative) inner.position = 'relative'
    if (absolute) inner.position = 'absolute'

    return <BaseBox {...inner} {...outter}/>
}

export default memo(Box)