import { BASEMAP } from "../index";
import { omit, includes } from '@ttungbmt/utils'
import { TileLayer, WMSTileLayer, LayersControl } from "../index";
import { mergeFilter, findIndex, toBool, get, str_contains, cloneDeep } from "@ttungbmt/utils";


function isTileLayerProps({type, options}) {
    let url = get(options, 'url', '')

    if((
        includes(['tile', 'wms'], type) ||
        (str_contains(url, '{x}') && str_contains(url, '{y}') && str_contains(url, '{z}') ) ||
        str_contains(url, 'geoserver/ows?')
    )) return true

    return false
}

export function layerFn({control, title, active, type, options, uuid, popupTemplate}) {
    const {BaseLayer, Overlay} = LayersControl
    let opts = cloneDeep(options)

    let boundary = get(window, 'store.state.map.boundary')
    if(control === 'basemap' && opts){
        opts.boundary = boundary
    }


    return {
        wrapper: control === BASEMAP ? BaseLayer : Overlay,
        wrapperProps: {
            name: title,
            checked: toBool(active)
        },
        layer: type2Component(type),
        layerProps: (isTileLayerProps({type, options})) ? ({
            uuid,
            ...opts,
        }) : ({
            uuid,
            type,
            data: options.url,
            options: omit(options, ['url', 'data']),
            popupTemplate
        })
    }
}

function type2Component(type) {
    switch (type) {
        case 'tile':
            return TileLayer;
        case 'wms':
            return WMSTileLayer;
        // default:
        //     return Source
    }
}

export function fnToggleLayer(state, uuid, key = 'active') {
    let index = findIndex(state, {uuid})

    if (state[index]) {
        state[index][key] = !state[index][key];
    }
}

export function fnDeactiveAllBasemap(state) {
    mergeFilter(state, {control: BASEMAP, active: true}, {active: false})
}
