import { put, delay, takeEvery, call } from 'redux-saga/effects'
import { actions as cabenhActs } from "../cabenh";
import { actions as mapActs } from "@ttungbmt/redux-leaflet"
import { api } from 'app/services'

export default function* cabenhSaga() {
    yield takeEvery(cabenhActs.clickCabenh.type, onClickCabenh)
    yield takeEvery(cabenhActs.clickDnc.type, onClickDnc)
    yield takeEvery(cabenhActs.showPopCabenh.type, showPopCabenh)
}

function* onClickCabenh({payload}) {
    yield call(onRowClick, {
        payload: {
            ...payload,
            _key: 'cabenh_sxh'
        }
    })
}

function* onRowClick({payload}) {
    const {geometry, ...props} = payload
    if (geometry) {
        yield put(mapActs.resetPopup())
        yield delay(10)
        yield put(mapActs.createPopup({key: payload._key, model: payload}))
        yield put(mapActs.setCenter(geometry))
    }
}

function* onClickDnc({payload}) {
    yield call(onRowClick, {
        payload: {
            ...payload,
            _key: 'dnc'
        }
    })
}

function* showPopCabenh({payload: {id}}) {
    try {
        const {data} = yield call(api.getCabenh, id)
        yield put(cabenhActs.clickCabenh(data.data))
    } catch (e) {
        console.error(e)
    }
}
