// @flow
import React, { memo, useEffect, Suspense } from 'react'
import { hot } from 'react-hot-loader/root';
import { AppContainer } from '@core/containers'
import { useSelector, useDispatch } from '@ttungbmt/redux-toolkit'
import { useInjectSaga, useInjectReducer } from '@core/utils/index'

import routes from './configs/routesConfig'
import Layouts from './layouts/Layouts'
import appSaga from './sagas/app.saga'
import layoutSaga from '@core/reducers/layout/saga'
import { actions as app_acts, selectors as app_sels } from '@core/reducers/app'
import { AppLoader } from '@core/components'
import ormReducer from './reducers/orm'
import { saga as cabenhSaga, KEY as cabenhKey } from './reducers/cabenh'
import { reducer as pcdReducer, saga as pcdSaga, KEY as pcdKey } from './reducers/pcd'
import { saga as odichSaga } from './reducers/odich'

import { LayoutProvider } from '@ttungbmt/redux-layout'

import MapPage from './layouts/map/MapPage'
import TreeChart from "./main/maps/views/cabenh/TreeChart";

function App() {
    const Layout = Layouts['map']
    const loaded = useSelector(app_sels.getLoaded)
    const dispatch = useDispatch()

    useInjectSaga({key: appSaga.key, saga: appSaga})
    useInjectSaga({key: layoutSaga.key, saga: layoutSaga})
    useInjectReducer({key: 'orm', reducer: ormReducer})

    useInjectReducer({key: pcdKey, reducer: pcdReducer})
    useInjectSaga({key: pcdKey, saga: pcdSaga})
    useInjectSaga({key: cabenhKey, saga: cabenhSaga})
    useInjectSaga({key: 'odich', saga: odichSaga})

    useEffect(() => {
        dispatch(app_acts.initApp())
    }, [])

    if (loaded) {
        return (
            <AppContainer>
                <Suspense fallback={<div>Loading...</div>}>
                    <LayoutProvider layouts={Layouts}>
                        <MapPage routes={routes}/>
                    </LayoutProvider>
                </Suspense>
            </AppContainer>
        )
    }
    return <AppLoader/>
}

export default memo(hot(App))
