import React, {memo} from 'react'
import { get, isEmpty } from '@ttungbmt/utils'
import { useFetch } from "@ttungbmt/react-hooks";
import arr2tree from 'array-to-tree'
import AmCharts from "@core/components/AmCharts";

const getColor = (index) => {
    switch (index) {
        case 0:
            return '#DA251D'
        case 1:
            return '#E7781D'
        case 2:
            return '#00923F'
        case 3:
            return '#0093DD'
        case 4:
            return '#443C92'
        case 5:
            return '#974578'
        default:
            return '#974578'
    }
}

const updateValue = (data = [], index = 0) => {
    return data.map(({children, ...d}) => {
        if(children) return {
            ...d,
            label: `F${index} - #${d.id}`,
            color: getColor(index),
            children: updateValue(children, index+1)
        }

        return {
            label: `F${index} - #${d.id}`,
            color: getColor(index),
            value: 1,
            ...d
        }
    })
}

function TreeChart({data: props}) {
    const {loading, data = []} = useFetch(`/admin/cabenh/get-tree?id=${props.id}`)

    let chartData = arr2tree(get(data, 'data.related', []), {
        parentProperty: 'parent_id', customID: 'id'
    })
        .filter(c => c.id !== 34)
    // 91, 97, 98, 120, 124, 125, 126, 127, 159, 158, 157,
    let model = get(data, 'data.model')


    let chartData1 = [
        {label: 'BUDDHA BAR', color: 'red', value: 2, parent_id: null, id: 0, children: updateValue(chartData)}
    ]

    // chartData = updateValue(chartData)


    console.log(chartData);

    return (
        <div>
            {loading && 'Loading...'}
            {!isEmpty(chartData) && <AmCharts data={chartData1}/>}
            {model && <div dangerouslySetInnerHTML={{__html: model.mota}} />}
        </div>
    )
}

export default memo(TreeChart)