import React, { memo, Fragment } from 'react'
import { useSelector, useRouteMatch } from '@ttungbmt/redux-toolkit'
import { FeatureGroup, Circle, Marker, Popup, PopManager, GeoJSON } from '@ttungbmt/redux-leaflet'
import { selectors as odichSels } from 'app/reducers/odich'
import { selectors as pcdSels } from 'app/reducers/pcd'

import { isEmpty } from '@ttungbmt/utils'
import { circle, union, feature } from '@turf/turf'

import features from 'app/features'
import moment from "app/utils/moment"

const CircleOdich = memo(({data, combineCir, radius, selected}) => {
    if(isEmpty(data)) return null

    if (combineCir) {
        const odichCir = union(...data.map(c => circle(feature(c.geometry), radius, {steps: 400, units: 'meters'})))
        return <GeoJSON data={odichCir.geometry} style={{color: selected ? '#ff7800' : '#3388ff'}}/>
    } else {
        return data.map(({position}, k) => (<Circle center={position} key={k} radius={radius}/>))
    }
})

const OdichLeaflet = memo(({id, cabenhs, range, options: {combineCir, radius}, selected}) => {
    if(isEmpty(cabenhs)) return null

    let data = cabenhs.filter(c => {
        let date = moment(c.ngaymacbenh_nv, 'L').unix()
        return date >= range.value[0] && date <= range.value[1]
    })

    return (
        <>
            <CircleOdich data={data} combineCir={combineCir} radius={radius} selected={selected}/>
            {data.map(({position, selected, ...props}, k) => {
                return (
                    <Fragment key={k}>
                        {selected && <Circle center={position} key={k} radius={radius} color={'red'} zIndex={800}/>}
                        <Marker position={position} selected={selected} riseOnHover={true}
                                zIndexOffset={selected ? 1000 : 100}>
                            <Popup>
                                <PopManager features={features} _key={'cabenh_sxh'} model={props}/>
                            </Popup>
                        </Marker>
                    </Fragment>
                )
            })}
        </>
    )
})

function OdichMap() {
    const options = useSelector(pcdSels.getOdichConf)
    const models = useSelector(odichSels.getOdichMap)

    if(isEmpty(models)) return <Fragment />

    return (
        <FeatureGroup>
            {models.map((m, k) => <OdichLeaflet key={k} {...m} options={options}/>)}
        </FeatureGroup>
    )
}

export default memo(OdichMap)