import React, { memo } from 'react'
import Button from '@material-ui/core/Button'

function BtnSubmit({children, loading, ...props}) {
    return (
        <Button disabled={loading} {...props}>
            {loading && <i className="icon-spinner2 spinner mr-1"/>} {children}
        </Button>
    )
}

BtnSubmit.defaultProps = {
    variant: 'contained',
    color: 'primary'
}

export default memo(BtnSubmit)