import { createSelector as ormCreateSelector } from 'redux-orm'
import orm from '../orm/orm'
import { toCenter } from '@ttungbmt/geo'



export const getCabenhById = ormCreateSelector(
    orm,
    (_, id) => id,
    ({Cabenh}, id) => {
        return Cabenh.filter({gid: id}).first()
    }
)

