import { map } from 'lodash-es'
import L from 'leaflet'

L.Control.Legend = L.Control.extend({
    _map: null,
    includes: L.Evented ? L.Evented.prototype : L.Mixin.Events,
    options: {
        position: 'topright',
        legends: [],   // array of legend entries - see README for format
        detectStretched: false,  // if true, will test to see if legend entries look stretched.  These are usually in sets of 3 with the middle element having no label.
        collapsed: true,
        label: 'Legend'
    },
    onAdd: function (map) {
        this._map = map;
        let container = L.DomUtil.create('div', 'leaflet-control leaflet-legend');
        this._container = container;

        // Disable events on container
        L.DomEvent.disableClickPropagation(container);
        if (!L.Browser.touch) {
            L.DomEvent.disableScrollPropagation(container);
        }

        this.render();

        return this._container;
    },

    // render this.options.entries
    render: function () {
        L.DomUtil.empty(this._container);
        let legends = this.options.legends,
            collapsed = this.options.collapsed,
            className = 'leaflet-legend'
        ;

        if (!legends) return;

        let link = L.DomUtil.create('a', className + '-toggle', this._container);
        link.href = '#';
        link.title = 'legend';
        link.innerHTML = this.options.label;

        if (L.Browser.touch) {
            L.DomEvent.on(link, 'click', L.DomEvent.stop);
            L.DomEvent.on(link, 'click', this.expand, this);
        } else {
            L.DomEvent.on(link, 'focus', this.expand, this);
        }

        if (collapsed) {
            this._map.on('click', this.collapse, this);

            if (!L.Browser.android) {
                L.DomEvent.on(this._container, {
                    'mouseover': this.expand,
                    'mouseout': this.collapse
                }, this);
            }
        }

        let list = L.DomUtil.create('div', className + '-list');

        legends.forEach(function (legend) {
            let items = legend.items;
            if (legend.visible === false) return null

            className = 'legend-block';

            if (this.options.detectStretched) {
                if (items && items.length === 3 && items[0].label !== '' && items[1].label === '' && items[2].label !== '') {
                    legend.type = 'stretched';
                }
            }

            if (legend.type === 'stretched') {
                className += ' legend-stretched';
            }

            let block = L.DomUtil.create('div', className, list);

            if (!items) {
                this._addElement(legend, block);
                return
            }

            if (legend.label) {
                let closedClass = legend.collapsed === true ? 'closed' : ''
                let header = L.DomUtil.create('h4', closedClass, block);
                L.DomUtil.create('div', 'caret', header);
                L.DomUtil.create('span', null, header).innerHTML = legend.label;

                L.DomEvent.on(header, 'click', function () {
                    if (L.DomUtil.hasClass(header, 'closed')) {
                        L.DomUtil.removeClass(header, 'closed');
                    }
                    else {
                        L.DomUtil.addClass(header, 'closed');
                    }
                }, this);
            }
            let elementContainer = L.DomUtil.create('div', 'legend-items', block);

            items.forEach(function (element) {
                if (element.visible === false) return null

                this._addElement(element, elementContainer);
            }, this);
        }, this);

        if (!collapsed) {
            this.expand();
        }

        this._container.appendChild(list)
    },

    _addElement: function (legend, container) {
        const {label, imageData, imageUrl, color, style, html} = legend

        let row = L.DomUtil.create('div', 'legend-row', container);
        if (imageData) {
            L.DomUtil.create('img', null, row).src = imageData;
        } else if (imageUrl) {
            let imgbox = L.DomUtil.create('div', 'leaflet-legend-image-url', row);
            imgbox.style.backgroundImage = `url(${imageUrl})`;
            imgbox.style.backgroundSize = '20px';
        } else if (color) {
            let colorbox = L.DomUtil.create('div', 'leaflet-legend-color', row)
            colorbox.style.backgroundColor = color
            map(style, (v, k) => colorbox.style[k] = v)
        } else if (html) {
            L.DomUtil.create('div', 'leaflet-legend-html', row).innerHTML = html;
        }

        if (!!label) {
            L.DomUtil.create('label', null, row).innerHTML = label;
        }
    },

    expand: function () {
        L.DomUtil.addClass(this._container, 'leaflet-legend-expanded');
        return this;
    },

    collapse: function () {
        L.DomUtil.removeClass(this._container, 'leaflet-legend-expanded');
        return this;
    },
});


L.control.legend = function (options) {
    return new L.Control.Legend(options);
};


export default L.control.legend