function PopupWMS({map, popup, children}) {
    const popupRef = useRef()
    usePopupInfo()
    // usePopupOnClick(popup || {}, popupRef)

    if (!popup) return
    if (!popup.position) return

    return (
        <Popup ref={popupRef} position={popup.position} minWidth={280}>
            <div dangerouslySetInnerHTML={{__html: popup.content}}/>
            {popup.model && cloneElement(children, popup)}
        </Popup>
    )
}

const withConnect = connect((state) => {
    return {
        popup: selectors.getCurrentPopup(state)
    }
});

export default compose(
    withConnect,
    memo,
)
(PopupWMS)