import React from 'react'
import { MapControl, withLeaflet } from 'react-leaflet'
import { Control } from 'leaflet'
import 'leaflet.polylinemeasure'
import 'leaflet.polylinemeasure/Leaflet.PolylineMeasure.css'

class RulerControl extends MapControl {
    createLeafletElement(props) {
        return new Control.PolylineMeasure(props)
    }
}

RulerControl.defaultProps = {
    unit: 'metres',
    showClearControl: true,
    showUnitControl: true,
    showBearings: false,
    clearMeasurementsOnStop: true,

    measureControlTitleOn: 'Bật thước đo',
    measureControlTitleOff: 'Tắt thước đo',
    measureControlLabel: '<i class="icon-rulers"/>',
    clearControlTitle: 'Clear Measurements',
}

export default withLeaflet(RulerControl)