import React, { memo } from 'react'
import { connect, compose, createSelector } from '@ttungbmt/redux-toolkit'

import { LayersManager, EventsManager, ControlsManager } from './components'
import { Map,  selectors as mapSels } from './index'

function LeafletContainer({children, mapOptions, loading}) {
    if (loading || !mapOptions.center) return null

    return (
        <>
            <Map {...mapOptions}>
                <LayersManager/>
                <ControlsManager/>
                <EventsManager/>
                {children}
            </Map>
        </>
    )
}

const makeMapOptions = createSelector([mapSels.getConfig, mapSels.getMapOptions], (config, options) => {
    return {
        style: {height: '100%', background: 'white'},
        ...options,
        ...config
    }
})

const mapStateToProps = (state) => ({
    mapOptions: makeMapOptions(state),
    loading: mapSels.getLoading(state)
})

const withConnect = connect(mapStateToProps)

export default compose(
    withConnect,
    memo
)(LeafletContainer);
