import React, { memo, useEffect } from 'react'
import { useSelector } from '@ttungbmt/redux-toolkit'
import { FeatureGroup, Circle, useLeaflet } from '@ttungbmt/redux-leaflet'
import { toCenter } from "@ttungbmt/geo";
import { get } from '@ttungbmt/utils'

function KhoanhVungMap() {
    const items = useSelector(state => get(state, 'pcd.khoanhvung', []))
    return (
        <FeatureGroup>
            {items.filter(i => i.geometry).map(({geometry}, k) => (
                <Circle center={toCenter(geometry)} key={k} radius={100}/>))}
        </FeatureGroup>
    )
}

export default memo(KhoanhVungMap)