import React, { Children, cloneElement, createElement, Fragment } from 'react'
import PropTypes from 'prop-types'
import { get, castArray, isString } from 'lodash-es'
import { getTemplates } from "./BaseLayout"

function useSlot(children) {
    const templates = getTemplates(children)
    let Slot = ({children: outerChildren = null, name, component, ...props}) => {
        const template = Slot.getTemplate(name)

        const children = template ? Children.map(castArray(template.props.children), child => {
            if(!child) return false
            child = isString(child) ? createElement(Fragment, {}, child) : child

            let name = get(child, 'type.name')
            return cloneElement(child, name ? props : {})
        }) : outerChildren
        return children
    }
    Slot.getTemplate = (slot) => templates[slot] || false
    Slot.propTypes = {
        name: PropTypes.string.isRequired,
    }
    Slot.defaultProps = {
        component: 'div'
    }

    return [Slot]
}

export default useSlot