import { update, findIndex, findKey, isArray, get, isFunction } from 'lodash-es'

function replaceFilterIn(object, path, predicate, updater) {
    let item = get(object, path)
    let key = null
    if(isArray(item)){
        key = findIndex(item, predicate)
    } else {
        key = findKey(item, predicate)
    }

    if(key >= 0){
        update(object, `${path}.${key}`, v => {
            if(isFunction(updater)) return updater(v)
            return updater
        })
    }

    return object
}

export default replaceFilterIn