import React, { memo, Fragment  } from 'react'
import { useSelector } from '@ttungbmt/redux-toolkit'
import { isEmpty, get } from '@ttungbmt/utils'
import { LayersControl, selectors as sels } from '../../index'


function LayersManager() {
    const layersControl = useSelector(sels.getLayersControl)
    const props = useSelector(state => sels.getControl(state, 'layers') || {})
    if (isEmpty(layersControl) || !props || get(props, 'enabled') === false) return <Fragment/>

    return (
        <LayersControl {...props}>
            {layersControl.map(({wrapper: Wrapper, wrapperProps, layer: Layer, layerProps}, k) => {
                return (
                    <Wrapper key={k} {...wrapperProps} checked={wrapperProps.checked}>
                        <Layer {...layerProps}/>
                    </Wrapper>
                )
            })}
        </LayersControl>
    )
}

export default memo(LayersManager)