import React, {memo, createElement, useContext} from 'react'
import PropTypes from 'prop-types'
import { LayoutContext } from "./LayoutProvider";

function Slot({children: outerChildren = null, name, component, ...props}) {
    const context = useContext(LayoutContext)
    if(!context.layout) return <></>

    const template = context.getTemplate(name)
    const children = template ? template.props.children : outerChildren
    return createElement(component, props, children);
}

Slot.propTypes = {
    name: PropTypes.string,
    component: PropTypes.any,
}

Slot.defaultProps = {
    component: 'div',
}

export default memo(Slot)