import React, { memo } from 'react'
import { Marker as BaseMarker } from 'react-leaflet'
import L from 'leaflet'

import 'leaflet-extra-markers'

const getIcon = ({selected}) => {
    let options = {
        icon: 'fa-circle-o',
        markerColor: selected ? 'orange': 'cyan',
        shape: selected ? 'square' : 'circle',
        prefix: 'fa',
    }

    return L.ExtraMarkers.icon(options)
}

function Marker({children, ...props}) {
    return (
        <BaseMarker icon={getIcon(props)} {...props}>
            {children}
        </BaseMarker>
    )
}

export default memo(Marker)