import React, { memo, useEffect, useRef, useMemo } from 'react'
import { useImmer } from 'use-immer'
import { jsPanel } from 'jspanel4'
import 'jspanel4/dist/jspanel.min.css'

function JsPanel({isOpen, setOpen, children, title, ...props}) {
    let contenRef = useRef(),
        panelRef = useRef()

    const [opts, setOpts] = useImmer({
        width: null, height: null,
    })

    useEffect(() => {
        if(isOpen){
            panelRef.current = jsPanel.create({
                theme: {
                    // bgPanel: '#03a9f4',
                    bgPanel: 'url("https://jspanel.de/img/trianglify-primary.svg") right bottom',
                    colorHeader: '#ffffff',
                    // bgContent: '#67989A',
                    // colorContent: '#ffffff'
                },
                // headerToolbar: 'header toolbar ...',
                // footerToolbar: 'footer toolbar ...',
                iconfont: 'material-icons',
                headerLogo: `<i class='icon-stack2 ml-2'></i>`,
                headerTitle: title,
                closeOnEscape: true,
                headerControls: {
                    smallify: 'remove',
                    size: 'md'
                },
                borderRadius: '4px',
                position: 'center-top 0 58',
                content: contenRef.current,
                resizeit: {
                    minWidth: 350,
                    minHeight: 200,
                    stop: function (panel, paneldata, e) {
                        setOpts(state => {
                            state.width = paneldata.width
                            state.height = paneldata.height
                        })
                    }
                },
                onclosed: function(panel, closedByUser){
                    panelRef.current = undefined
                    setOpen(false)
                },
                // contentSize: {
                //     width: 'auto',
                //     height: 'auto'
                // }
                // panelSize: {
                //     width: 'auto',
                //     height: 'auto'
                // }
                // setStatus: 'maximized'
                    ...props
            })

            console.log(props);

            if(opts.width || opts.height) panelRef.current.resize({width: opts.width, height: opts.height}).reposition()
        } else {
            console.log(panelRef.current);
            panelRef.current && panelRef.current.close()
        }

        return () => {

        }
    }, [isOpen])

    // useEffect(() => {
    //     console.log(opts.width || opts.height);
    //
    // }, [opts.width, opts.height])

    return (
        <div ref={contenRef} style={{display: isOpen ? 'inherit' : 'none'}}>
            {children}
        </div>
    )
}

JsPanel.defaultProps = {
    title: 'My Panel'
}

export default memo(JsPanel)