import { put, call, takeEvery, delay } from 'redux-saga/effects'
import axios from 'axios'
import { actions as authActs } from '@core/reducers/auth'
import {actions as appActs } from '@core/reducers/app'
import {actions as layoutActs } from '@core/reducers/layout'
import {actions as mapActs } from '@ttungbmt/redux-leaflet'
import store from 'app/Store'

export default function* appSaga() {
    yield takeEvery('@app/INIT', initApp)
}

appSaga.key = 'app'

function* initApp() {
    try {
        const {data} = yield call(axios.get, '/maps/configs');
        store.commit('setCat', data.dm)
        store.commit('setMap', data.map)
        yield put(authActs.setAuth(data.auth))
        yield put(layoutActs.setLayout(data.app))
        yield put(appActs.setApp(data.app))
        yield delay(1000)
        yield put(mapActs.setMap(data.map))
    } catch (e) {
        console.error(e)
    }
}


