import { TileLayer as LeafletTileLayer } from 'leaflet'
import { withLeaflet, GridLayer } from 'react-leaflet'
import { withBoundary } from '../../libs/BoundaryCanvas'

class TileLayer extends GridLayer {
    createLeafletElement({boundary, ...props}) {
        const { url, ...params } = props
        const { leaflet: _l, ...options } = this.getOptions(params)
        return withBoundary(new LeafletTileLayer(url, options), {boundary})
    }

    updateLeafletElement(fromProps: Props, toProps: Props) {
        super.updateLeafletElement(fromProps, toProps)
        if (toProps.url !== fromProps.url) {
            this.leafletElement.setUrl(toProps.url)
        }
    }
}

export default withLeaflet(TileLayer)