import { takeEvery } from "redux-saga/effects"
import {actions as acts} from '@core/reducers/layout'

import {name} from './slice'
import { emitter } from '@ttungbmt/redux-toolkit'

export default function* layoutSaga() {
    yield takeEvery(acts.resizeSidebar.type, resizeSb)

}

layoutSaga.key = name

function* resizeSb() {
    emitter.emit(acts.resizeSidebar.type)
}

