import React, { memo, useEffect, useRef } from 'react'
import { useDidMount, useModal } from '@ttungbmt/react-hooks'
import { emitter } from '@ttungbmt/redux-toolkit'
import { Modal, ProgressBar, Button } from "react-bootstrap"
import { useLeaflet } from 'react-leaflet'
import leafletImage from 'leaflet-image'
import ReactCrop from 'react-image-crop';
import 'react-image-crop/dist/ReactCrop.css'
import { useImmer } from 'use-immer'
import FileSaver from 'file-saver'

const {Header, Title, Body, Footer} = Modal

function getCroppedImg(image, pixelCrop) {

    const canvas = document.createElement('canvas');
    canvas.width = pixelCrop.width;
    canvas.height = pixelCrop.height;
    const ctx = canvas.getContext('2d');

    ctx.drawImage(image, pixelCrop.x, pixelCrop.y, pixelCrop.width, pixelCrop.height, 0, 0, pixelCrop.width, pixelCrop.height);

    return canvas.toDataURL()
}

function ExportMapModal({isOpen, onClose, map, ...props}) {
    const [{loading, src, crop}, setSrc] = useImmer({loading: true, src: null, crop: {unit: '%'}, croppedSrc: null})
    const imageRef = useRef()

    const onCropChange = crop => setSrc(state => { state.crop = crop })
    const onImageLoaded = image => imageRef.current = image

    const onSave = () => {
        let croppedImgSrc = (crop.width === 0 && crop.height === 0) ? src : getCroppedImg(imageRef.current, crop)
        FileSaver.saveAs(croppedImgSrc, 'Bando.jpg')
        onClose()
    }

    useEffect(() => {
        leafletImage(map, function (err, canvas) {
            setSrc(state => {
                state.loading = false
                state.src = canvas.toDataURL()
            })
        })
    }, [])


    return (
        <Modal show={isOpen} onHide={onClose} dialogClassName="modal-export-map-w">
            <Header className="bordered" closeButton>
                <Title className="font-weight-semibold">
                    Xuất bản đồ
                </Title>
            </Header>

            <Body style={{padding: '12px 0'}}>
            {loading ? (<ProgressBar animated now={100} className="m-2"/>) : (
                <ReactCrop
                    src={src}
                    ruleOfThirds
                    crop={crop}
                    onChange={onCropChange}
                    onImageLoaded={onImageLoaded}
                />
            )}
            </Body>
            <Footer>
                <Button variant="secondary" onClick={onClose}>Đóng</Button>
                {!loading && <Button variant="primary" onClick={onSave}>Lưu file</Button>}
            </Footer>
        </Modal>
    )
}

function ExportMapControl() {
    const {map} = useLeaflet()

    const [onShow, onHide] = useModal(ExportMapModal, {map})

    useDidMount(() => {
        emitter.on('exportMap', onShow)
    })
}

export default memo(ExportMapControl)