import { put, delay, takeEvery, select, take, fork, cancel, call, takeLatest } from 'redux-saga/effects'
import { selectors as mapSels } from '@ttungbmt/redux-leaflet'
import { push } from 'connected-react-router'
import { map, get, flatten } from '@ttungbmt/utils'
import axios from 'axios'

import { actions as cabenhActs, selectors as cabenhSels } from '../cabenh'
import { actions as odichActs, selectors as odichSels } from './index'
import { actions as mapActs } from "@ttungbmt/redux-leaflet";
import { isEmpty, head, uniq } from '@ttungbmt/utils'

import { toCenter, toBounds } from '@ttungbmt/geo'
import { getPathname } from "@core/selectors/router"
import { getRangeDate } from "app/utils";

export default function* odichSaga() {
    yield takeEvery(odichActs.khoanhVung.type, onKhoanhVung)
    yield takeEvery(odichActs.clickOdich.type, clickOdich)
    yield takeEvery(odichActs.panToCabenhCir.type, panToCabenhCir)
    yield takeLatest(odichActs.searchSemiAutoOD.type, searchSemiAutoOD)
    yield takeLatest(odichActs.createAutoOD.type, createAutoOD)
    yield takeLatest(odichActs.showPopDnc.type, showPopDnc)

    yield runSliderOdich()
}

function* onKhoanhVung({payload: id}) {
    const item = yield select(state => mapSels.getPopItemById(state, id))
    const cabenh = yield select(state => cabenhSels.getCabenhById(state, id))

    if (cabenh) {
        alert(`Ca bệnh này đã được khoanh vùng`)
        return null
    }

    const pathname = yield select(getPathname)
    if (pathname === '/maps/odich/view/0') {
        yield put(odichActs.addCabenh({id: 0, cabenhs: [item.model]}))
        return null
    }


    yield put(odichActs.removeAll())
    yield delay(10)
    yield put(odichActs.create({id: 0, source: 'wms', cabenhs: [item.model]}))
    yield put(push('/maps/odich/view/0'))
}

function* clickOdich({payload: {redirect, ...payload}}) {
    if(redirect)  yield put(push(redirect))
    yield put(mapActs.resetPopup())
    yield delay(5)

    const id = payload.id
    let bounds, geoArr, model = payload.props

    if(id === 0){
        let cabenhs = payload.cabenhs.filter(c => c.geometry)
        yield put(odichActs.removeAll({id}))

        if(isEmpty(cabenhs)) return null
        geoArr = map(cabenhs, 'geometry')
        bounds = toBounds(geoArr)

        yield put(odichActs.create({id: 0, source: 'list', cabenhs, odich_id: model.gid}))
    } else if(id > 0){
        let cabenhs = yield select(state => odichSels.getCabenhs(state, id))
        if(isEmpty(cabenhs)) return null

        geoArr = map(cabenhs, 'geometry')
        bounds = toBounds(geoArr)
        yield put(odichActs.selected(id))
    }

    if(geoArr) yield put(mapActs.createPopup({key: 'odich', model: {...model, geometry: toCenter(geoArr, 'geometry')}}))
    if(bounds) {
        yield delay(2)
        yield put(mapActs.setBounds(bounds))
    }
}

function* panToCabenhCir({payload}) {
    const {geometry, id} = payload
    yield put(cabenhActs.selected(id))
    yield put(mapActs.setCenter(geometry))

}

function* showPopDnc({payload}) {
    if(!payload.geometry) return null
    yield put(mapActs.resetPopup())
    yield delay(5)
    yield put(mapActs.createPopup({key: 'dnc', model: payload}))
    yield put(mapActs.setCenter(payload.geometry))
}

function* changeRangeValue({payload}) {
    let cabenhs = yield select(state => odichSels.getCabenhs(state, payload.id)),
        dates = uniq(getRangeDate(cabenhs)),
        index = 0


    while (true) {
        yield delay(1000)
        if (dates[index]) {
            yield put(odichActs.updateRangeValue({id: payload.id, value: [head(dates), dates[index]]}))
            index++
        } else {
            yield put(odichActs.stopSlider(payload))
        }

    }
}

function* runSliderOdich() {
    while (true) {
        // starts the task in the background
        const action = yield take(odichActs.runSlider.type)
        const bgSyncTask = yield fork(changeRangeValue, {payload: action.payload})
        // wait for the user stop action
        yield take(odichActs.stopSlider.type)
        // user clicked stop. cancel the background task
        // this will throw a SagaCancellationException into task
        yield cancel(bgSyncTask)
    }
}


function* searchSemiAutoOD({payload}) {
    let cabenhIds = map(payload.cabenhs, 'gid')
    yield put(odichActs.setLoading({value: true, id: 0}))

    try {
        const {data} = yield call(axios.post, `/api/odich/tim-cabenh`, {cabenhIds})
        if (!isEmpty(get(data, 'odichs', []))) {
            let cabenhs = get(data, 'odichs.0.cabenhs', []).map(c => get(data.cabenhs, c.id))
            yield put(cabenhActs.remove(cabenhIds))
            yield put(odichActs.addCabenh({id: payload.id, source: 'tool', cabenhs}))
        }

        yield put(odichActs.setLoading({value: false, id: 0}))
    } catch (e) {
        yield put(odichActs.setLoading({value: false, id: 0}))
        console.error(e)
    }
}

function* createAutoOD({payload: data}) {
    let models = transformOdich(data),
        geomArr = map(flatten(map(models, 'cabenhs')).filter(g => g.geometry), 'geometry'),
        bounds = toBounds(geomArr)


    yield put(odichActs.removeAll())
    yield put(odichActs.create(models))

    if(bounds) yield put(mapActs.setBounds(bounds))
}

function transformOdich(data) {
    let dbCabenhs = data.cabenhs

    return map(data.odichs).map(({cabenhs}, k) => {
        return {
            id: k+1,
            source: 'tool',
            cabenhs: cabenhs.map(({id: gid, ...pc}) => {
                return {
                    ...get(dbCabenhs, gid, {}),
                    ...pc,
                    gid,
                }
            })
        }
    })
}



