import React, { memo, useRef } from 'react'
import styled from 'styled-components'
import LoadingBar from 'react-redux-loading-bar'
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles'
import { ModalProvider } from "@ttungbmt/react-hooks"
import { hooks as layout_hooks } from '@core/reducers/layout'
import blue from '@material-ui/core/colors/blue'

const theme = createMuiTheme({
    palette: {
        primary: {
            main: blue[500],
            dark: blue[700],
        },
    },
    typography: {
        fontSize: 13
    }
})

const Wrapper = styled.div`
    
`;

function AppContainer({children}) {
    const wrapperRef = useRef()
    layout_hooks.useFullscreen({ref: wrapperRef})

    return (
        <ThemeProvider theme={theme}>
                <ModalProvider>
                    <Wrapper id="app-container" ref={wrapperRef}>
                        <LoadingBar style={{backgroundColor: '#f44336', height: 2, zIndex: 999999}}/>
                        {children}
                    </Wrapper>
                </ModalProvider>

        </ThemeProvider>
    )
}

export default memo(AppContainer)