import * as acts from './odich.actions'
import { head, last, isArray, isPlainObject } from '@ttungbmt/utils'

import { getRangeDate } from 'app/utils'

function createOdich({Odich, Cabenh}, {id, cabenhs, source, ...props}) {
    let dates = getRangeDate(cabenhs)

    const model = Odich.create({
        ...props,
        id,
        source,
        range: {
            minMax: [head(dates), last(dates)],
            value: [head(dates), last(dates)]
        }
    })
    cabenhs.map(c => model.cabenhs.add(Cabenh.create(c)))
}

const odichSlice = {
    caseReducers: {
        [acts.addCabenh.type]: ({payload}, Odich, {Cabenh}) => {
            let model = Odich.withId(payload.id)
            if(!model) model = Odich.create(payload.id)

            payload.cabenhs.map(c => model.cabenhs.add(Cabenh.create(c)))

            let dates = getRangeDate(model.cabenhs.all().toRefArray())

            model.update({
                source: payload.source,
                range: {
                    minMax: [head(dates), last(dates)],
                    value: [head(dates), last(dates)]
                }
            })
        },

        [acts.create.type]: ({payload}, Odich, session) => {
            if(isArray(payload)) {
                payload.map(i => createOdich(session, i))
            } else {
                createOdich(session, payload)
            }
        },
        [acts.update.type]: ({payload}, Odich) => {
            Odich.withId(payload.id).update({range: payload.range})
        },
        [acts.remove.type]: ({payload: id}, Odich) => {
            const model = Odich.withId(id)
            model.cabenhs.all().delete()
            model.delete()
        },
        [acts.removeAll.type]: ({payload}, Odich) => {
            let models = Odich.all()
            if(isPlainObject(payload)){
                models.filter(payload)
            }

            models.toModelArray().map(m => m.cabenhs.all().delete())
            models.delete()
        },
        [acts.updateRangeValue.type]: ({payload}, Odich) => {
            const model = Odich.withId(payload.id)
            model.update({range: {...model.range, value: payload.value}})
        },
        [acts.runSlider.type]: ({payload}, Odich) => {
            Odich.withId(payload.id).update({runStatus: 'Running'})
        },
        [acts.stopSlider.type]: ({payload}, Odich) => {
            Odich.withId(payload.id).update({runStatus: 'Stopped'})
        },
        [acts.setLoading.type]: ({payload}, Odich) => {
            Odich.withId(payload.id).update({loading: payload.value})
        },
        [acts.selected.type]: ({payload: id}, Odich) => {
            Odich.all().filter(({id}) => id > 0).update({selected: false})
            Odich.withId(id).update({selected: true})
        },
    }
}

export default odichSlice