import React, {memo} from 'react'
import { castArray, get } from "lodash-es"

export const isSlot = (o) => get(o, 'type.type.name') === 'Slot'
export const filterSlot = (arr, name) => arr.filter(o => isSlot(o) && o.props.name === name)

export const hasChildren = p => {
    const arr = castArray(p)
    for (let i in arr) {
        if(get(arr[i], 'props.children')){
            return true
        }
    }

    return false
}

function Slot({children}) {
    return (
        <>
            {children}
        </>
    )
}

export default memo(Slot)