import React, { memo } from 'react'
import * as Space from "react-spaces"
import { useSelector, useDispatch } from '@ttungbmt/redux-toolkit'
import { selectors as layout_sels, actions as layout_acts, utils } from '@core/reducers/layout'
import {Slot} from '@ttungbmt/redux-layout'

import Wrapper from './Wrapper'

function Sidebar({children}) {
    const dispatch = useDispatch()
    const sidebarProps = useSelector(layout_sels.getSidebar)

    return (
        <Space.LeftResizable
            as="aside"
            onResizeEnd={() => dispatch(layout_acts.resizeSidebar())}
            {...utils.pickSpace(sidebarProps, ['size', 'maximumSize', 'handleSize'])}
        >
            <Wrapper>
                <Slot name="main" />
            </Wrapper>
        </Space.LeftResizable>
    )
}

export default memo(Sidebar)