import React, { memo } from 'react'
import { Nav } from 'react-bootstrap'
import {BaseLayout, Template} from '@ttungbmt/redux-layout'
import { renderRoutes } from "react-router-config"
import { useSelector } from '@ttungbmt/redux-toolkit'
import { selectors as app_sels } from "@core/reducers/app"
import { selectors as layout_sels } from "@core/reducers/layout"

import { NavLink } from "./components"

function MapPage({routes}) {
    const [app, menuItems] = useSelector(state => [app_sels.getApp(state), layout_sels.getMenu(state)])

    return (
        <BaseLayout layout="map">
            <Template slot="header.logo">
                <img src={app.app_logo} alt="" style={{height: '45px'}}/>
            </Template>
            <Template slot="header.title">
                 <span>{app.app_name}</span>
            </Template>
            <Template slot="header.menu">
                <Nav>
                    {menuItems && menuItems.map((i, k) => <NavLink key={k} {...i}/>)}
                </Nav>
            </Template>
            <Template slot="main">
                {renderRoutes(routes)}
            </Template>


        </BaseLayout>
    )
}

export default memo(MapPage)