import React, { memo, useEffect } from 'react'
import { useLeaflet } from 'react-leaflet'
import { emitter, useDispatch, useSelector } from "@ttungbmt/redux-toolkit"
import { selectors as mapSels, actions as mapActs } from '../../index'

function useUpdateZoom(map) {
    const dispatch = useDispatch()
    const zoom = useSelector(mapSels.getZoom)

    useEffect(() => {
        if(zoom && zoom !== map.getZoom()) dispatch(mapActs.setZoom(map.getZoom()))

        map.on('zoomend', (e) => dispatch(mapActs.setZoom(e.target.getZoom())))
    }, [])
}

function useUpdateMapSize(map) {
    const updateMapSize = (delay = 0) => setTimeout(() => map.invalidateSize(), delay)

    useEffect(() => {
        updateMapSize()
        let onResizeSb = emitter.on(`@layout/RESIZE_SIDEBAR`, updateMapSize)
        let onToggleMapFullscreen = emitter.on(`@layout/TOGGLE_MAP_FULLSCREEN`, () => updateMapSize(400))

        return () => {
            onResizeSb.remove()
            onToggleMapFullscreen.remove()
        }
    }, [])
}



function EventsManager() {
    const {map} = useLeaflet()
    useUpdateZoom(map)
    useUpdateMapSize(map)


    return <></>
}

export default memo(EventsManager)