import styled from 'styled-components'

const Wrapper = styled.div`
    position: relative;
    height: 100%;
    border-right: 1px solid hsla(0,0%,87.5%,.56);

    .sb-title { 
        border-bottom: 1px solid #dfdfdf;
        font-weight: 500; 
        .badge {
            padding: 3px 4px;
            i {font-size: 14px}
        }
    }
    
     .sb-sub-title { 
        border-bottom: 1px solid #dfdfdf;
        > div {
        }
        button, a {
            outline: none;
            width: 35px;
            height: 100%;
            &:hover {background: hsla(0,0%,93.7%,.73)}
        }
    }
`;

export default Wrapper