import React, { memo } from 'react'
import { useImmer } from 'use-immer'
import { get } from 'lodash-es'

const defaultState = {}

export const LayoutContext = React.createContext(defaultState)

function LayoutProvider({children, layouts}) {
    const [state, setState] = useImmer({
        layout: null,
        templates: null
    })

    const getLayout = (name) => layouts[name]
    const getTemplate = (slot) => state.templates[slot] || false
    const setLayout = ({name, templates}) => {
        setState(draft => {
            draft.layout = name
            draft.templates = templates
        })
    }


    const value = {
        layout: state.layout,
        layouts,
        templates: state.templates,
        getLayout,
        getTemplate,
        setLayout
    }

    return (
        <LayoutContext.Provider value={value}>
            {children}
        </LayoutContext.Provider>
    )
}
export default memo(LayoutProvider)
