import { GeoJSON as LeafletGeoJSON, type LatLng, type Layer } from 'leaflet'

import { withLeaflet, Path } from 'react-leaflet'
import { isEqual } from '@ttungbmt/utils'

class GeoJSON extends Path {
    createLeafletElement(props) {
        return new LeafletGeoJSON(props.data, this.getOptions(props))
    }

    updateLeafletElement(fromProps, toProps) {
        if (typeof toProps.style === 'function') {
            this.leafletElement.setStyle(toProps.style)
        } else {
            this.setStyleIfChanged(fromProps, toProps)
        }

        if (!isEqual(toProps.data, this.getPathOptions(fromProps.data))) {
            this.layerContainer.removeLayer(this.leafletElement)
            this.leafletElement = this.createLeafletElement(toProps)
            this.layerContainer.addLayer(this.leafletElement)
        }
    }
}

export default withLeaflet(GeoJSON)