import React, { memo } from 'react'

function Pop(props = {}) {
    let link = '/sxh/odich/update?id=' + props.gid

    let items = [
        {title: 'Ngày mắc bệnh ca đầu tiên', field: 'ngaymacbenh_1'},
        {title: 'Ngày xác định', field: 'ngayxacdinh'},
        {title: 'Khu phố', field: 'khupho'},
        {title: 'PX', field: 'px'},
        {title: 'QH', field: 'qh'},
        {title: 'Ngày dự kiến kết thúc', field: 'ngaydukienkt'},
        {title: 'Ngày ca gần nhất', field: 'ngaymacbenh_last'},
        {title: 'Số ca', field: 'soca'},
        {title: 'Số lần xử lý', field: 'solan_xl'},
        {title: 'Tình trạng', field: 'tinhtrang'},
    ]

    return (
        <div>
            <div className="pop-wrapper">
                <div className="pop-title text-uppercase">
                    <a href={link} target="_blank">Ổ dịch #{props.id} ({props.soca})</a>
                </div>
                <div className="pop-body">
                    <ul className="list-data mb-10">
                        {items.map(({title, field}, k) => props[field] ? (<li key={k}><b>{title}:</b> <span>{props[field]}</span></li>) : '')}
                    </ul>
                    <ul className="list-actions list-inline mb-0 mt-1">
                        <li className="list-inline-item"><a href={link} target="_blank">Chi tiết</a></li>
                    </ul>
                </div>
            </div>

        </div>
    )
}

export default memo(Pop)