import { createSelector as ormCreateSelector } from 'redux-orm'
import orm from 'app/reducers/orm/orm'
import { toCenter } from "@ttungbmt/geo";
import { get, isArray, isNumber } from '@ttungbmt/utils'
import { selectors as pcdSels } from "app/reducers/pcd";
import { matchPath } from 'react-router'


export const getOdich = ormCreateSelector(
    orm.Odich,
    orm.Odich.cabenhs,
    (odichs, cabenhs) => {
        if (!odichs) return {}

        let odFn = (o, cb) => ({
            ...o,
            cabenhs: cb.map(c => ({
                ...c,
            }))
        })

        if (isArray(odichs)) return odichs.map((o, k) => odFn(o, get(cabenhs, k, [])))
        return odFn(odichs, cabenhs)
    }
)

export const getRange = ormCreateSelector(
    orm.Odich.range,
    (range) => range ? range : ({
        minMax: [],
        value: [],
    })
)

export const getCabenhs = ormCreateSelector(
    orm.Odich.cabenhs,
    (cabenhs) => cabenhs ? cabenhs.map(c => ({...c, position: toCenter(c.geometry)})) : []
)

const getPathname = (state) => state.router.location.pathname

export const getOdichMap = ormCreateSelector(
    orm.Odich,
    orm.Odich.cabenhs,
    pcdSels.getOdichConf,
    getPathname,
    (odichs, cabenhs, options, pathname) => {
        let id = get(matchPath(pathname, {path: '/maps/odich/view/:id'}), 'params.id')

        return odichs
            .map((o, k) => ({
                ...o,
                options,
                cabenhs: get(cabenhs, k, []).filter(c => c.geometry).map(c => ({
                    ...c,
                    position: toCenter(c.geometry)
                }))
            }))
            .filter(o => {
                return id ? o.id === parseInt(id) : true
            })
    }
)



