import { takeEvery, takeLatest, put, call, select, delay } from 'redux-saga/effects'
import { map, isString, orderBy, find, get, includes, isEmpty } from '@ttungbmt/utils'
import { selectors as mapSels } from '../index'
import $ from 'jquery'
import axios from 'axios'
import { toBounds } from "@ttungbmt/geo";

import { emitter } from '@ttungbmt/redux-toolkit'
import { actions as mapActs } from "../index"

export function* mapSaga() {

    yield takeEvery('layout/toggleMapFullscreen', toggleMapFullscreen)
    yield takeEvery(mapActs.setMap.type, setMap)
    // yield debounce(1000, mapActs.getWMSInfo.type, getWMSInfo)
    yield takeLatest(mapActs.getWMSInfo.type, getWMSInfo)
    yield takeEvery(mapActs.setMapLoading.type, setMapLoading)
    yield takeEvery(mapActs.setMapLoaded.type, setMapLoaded)
}

function *setMap({payload}) {
    const {layer_tree, popup, extent, boundary, basemap} = payload
    yield put(mapActs.setLoading(true))

    if (layer_tree) yield put(mapActs.setLayers(layer_tree))
    if (popup) yield put(mapActs.setPopupKeys(popup))

    if(!basemap) yield put(mapActs.addBasemap(['google', 'openstreet', 'hcmgis', 'lidar', 'satellite'], 'hcmgis'))
    // if (boundary) {
    //     yield put(mapActs.clipBasemap(boundary))
    // }

    if (extent) yield put(mapActs.setBounds(toBounds([extent])))

    yield put(mapActs.setLoading(false))
}

function* getWMSInfo({payload}) {
    const ids = map(payload.items, 'uuid')
    const layers = yield select(state => mapSels.getLayersById(state, ids))
    const keys = yield select(mapSels.getPopupKeys)

    let postData = {
        ...payload,
        items: orderBy(payload.items, ['zIndex'], ['desc'])
            .map(i => {
                let key = get(find(layers, {uuid: i.uuid}), 'key')
                return {key, ...i}
            })
            .filter(i => includes(keys, i.key))
    }

    if(isEmpty(postData.items)) return null


    yield put(mapActs.setMapLoading())
    yield put(mapActs.setPopup(postData))

    try {
        const {data} = yield call(axios.post, '/maps/get-wms-info', postData)
        yield put(mapActs.setMapLoaded())

        if (isString(data) || !data.data) return null
        yield put(mapActs.updatePopupContent(data.data))
    } catch (e) {
        yield put(mapActs.setMapLoaded())
        console.error(e);
    }
}

function* toggleMapFullscreen() {
    emitter.emit('@layout/TOGGLE_MAP_FULLSCREEN')
}

function setMapLoading({payload = '.leaflet-container'}) {
    $(payload).css('cursor', 'progress')
    $('.leaflet-interactive').css('cursor', 'progress')
}

function setMapLoaded({payload = '.leaflet-container'}) {
    $(payload).css('cursor', 'default')
    $('.leaflet-interactive').css('cursor', 'pointer')
}


