import React, { memo } from 'react'
import { useDispatch, useSelector } from '@ttungbmt/redux-toolkit'
import { actions as odichActs } from '../../reducers/odich'
import { actions as cabenhActs, selectors as cabenhSels } from '../../reducers/cabenh'

function Pop(props) {
    const dispatch = useDispatch()
    const cabenh = useSelector(state => cabenhSels.getCabenhById(state, props.gid))
    let link = '/sxh/default/update?id=' + props.gid

    return (
        <div>
            <div className="pop-wrapper">
                <div className="pop-title text-uppercase">
                    <a href={link} target="_blank">
                        <div> {props.hoten} (<span className="text-capitalize"></span>)</div>
                    </a>
                </div>
                <div className="pop-body">
                    <ul className="list-data mb-10">
                        <li><b>Họ tên:</b> <span>{props.hoten}</span></li>
                        <li><b>Địa chỉ:</b> <span>{props.diachi} - {props.tenquan} - {props.tenphuong}</span></li>
                        <li><b>Khu phố - Tổ:</b> <span>{props.khupho_to}</span></li>
                        <li><b>Ngày mắc bệnh:</b> <span>{props.ngaymacbenh}</span></li>
                        <li><b>Ngày xuất viện:</b> <span>{props.ngayxuatvien}</span></li>
                        <li><b>Ngày báo cáo:</b> <span>{props.ngaybaocao}</span></li>
                    </ul>
                    <ul className="list-actions list-inline mb-0 mt-1">
                        <li className="list-inline-item"><a href={link} target="_blank">Chi tiết</a></li>
                        <li className="list-inline-item"><a onClick={() => dispatch(odichActs.khoanhVung(props.gid))}>Khoanh
                            vùng</a></li>
                        {cabenh && <li className="list-inline-item"><a className="text-danger"
                                                                       onClick={() => dispatch(cabenhActs.deleteCabenhById(props.gid))}><i
                            className="icon-bin"/> Xóa</a></li>}
                    </ul>
                </div>
            </div>

        </div>
    )
}

export default memo(Pop)