import React, { memo } from 'react'
import { useInjectReducer, useInjectSaga } from '@core/utils/index'

import { LeafletContainer, sagas as mapSagas, reducer as mapReducer, PopupWMS, PopManager, LegendControl } from '@ttungbmt/redux-leaflet'

import '@ttungbmt/redux-leaflet/dist/redux-leaflet.cjs.css'

import OdichMap from './OdichMap'

import features from 'app/features'
import KhoanhVungMap from "./KhoanhVungMap";


const legends = [
    {
        "label": "Ca bệnh",
        "collapsed": true,
        "items": [
            {label: '<= 7', html: '<div class="lengend-circle" style="background: #ff0000"></div>'},
            {label: '8 - 14', html: '<div class="lengend-circle" style="background: #ff4600"></div>'},
            {label: '15 - 21', html: '<div class="lengend-circle" style="background: #007f00"></div>'},
            {label: '22 - 28', html: '<div class="lengend-circle" style="background: #0000ff"></div>'},
            {label: '>= 28', html: '<div class="lengend-circle" style="background: #8c8c8c"></div>'},
            {label: 'Zika', html: '<div class="lengend-circle" style="background: #aa2672"></div>'},
        ]
    },
    {
        "label": "Hình thức điều trị",
        "collapsed": true,
        "items": [
            {label: 'Nội trú', html: '<div class="lengend-circle" style="background: red"></div>'},
            {label: 'Ngoại trú', html: '<div class="lengend-triangle"></div>'},
        ]
    },
    {
        "label": "ĐNC",
        "collapsed": true,
        "items": [
            {label: 'Nhóm 1', html: '<div class="lengend-rectangle" style="background: #e81d62"></div>'},
            {label: 'Nhóm 2', html: '<div class="lengend-rectangle" style="background: #2196f3"></div>'},
            {label: 'Nhóm 3', html: '<div class="lengend-rectangle" style="background: #388e3c"></div>'},
        ]
    },
]

function Leaflet() {
    useInjectReducer({key: 'map', reducer: mapReducer})
    useInjectSaga({key: 'map', saga: mapSagas.map})

    return (
        <LeafletContainer>
            <OdichMap/>
            <KhoanhVungMap/>
            <LegendControl legends={legends} />

            <PopupWMS>
                <PopManager features={features}/>
            </PopupWMS>
        </LeafletContainer>
    )
}

export default memo(Leaflet)