import { Model, attr, many } from 'redux-orm'
import slice from '../reducers/cabenh/cabenh.slice'

class Cabenh extends Model {

    toString() {
        return `Cabenh: ${this.name}`
    }

}

Cabenh.modelName = 'Cabenh'
Cabenh.reducers = slice.caseReducers

Cabenh.fields = {
    id: attr(),

}

export default Cabenh