import { pick } from 'lodash-es'
import { createAction, createSelector } from '@ttungbmt/redux-toolkit'
import slice, { name } from './slice'
import * as layoutActions from './actions'
import * as layoutHooks from './hooks'

// SELECTORS
const getLayout = createSelector([name], r => r)
const getAppFullscreen = createSelector([getLayout], r => r.fullscreen)
const getMapFullscreen = createSelector([getLayout], r => r.map.fullscreen)
const getHeader = createSelector([getLayout], r => r.header)
const getMenu = createSelector([getLayout], r => r.header.menuItems)
const getSidebar = createSelector([getLayout], r => r.sidebar)
const getFooter = createSelector([getLayout], r => r.footer)

export const actions = {
    ...layoutActions,
    ...slice.actions
}

export const selectors = {
    getLayout,
    getAppFullscreen,
    getMapFullscreen,
    getHeader,
    getMenu,
    getSidebar,
    getFooter,
}

export const utils = {
    pickSpace(props) {
        return pick(props, ['size', 'handeSize', 'maximumSize'])
    }
}

export const hooks = {
    ...layoutHooks
}

export default slice.reducer