import React, { memo } from 'react'
import { useDispatch } from '@ttungbmt/redux-toolkit'
import { actions as pcdActs } from 'app/reducers/pcd'

function Pop(props) {
    const dispatch = useDispatch()
    let link = '/admin/cabenh/update?id=' + props.id

    const onKhoanhvung = () => dispatch(pcdActs.khoanhVung(props))

    return (
        <div>
            <div className="pop-wrapper">
                <div className="pop-title text-uppercase">
                    <a href={link} target="_blank">
                        <div> #{props.ma_qg} {props.hoten} (<span className="text-capitalize">{props.phanloai_text}</span>)</div>
                    </a>
                </div>
                <div className="pop-body">
                    <ul className="list-data mb-10">
                        <li><b>Địa chỉ:</b> <span>{props.diachi}</span></li>
                        <li><b>Nơi phát hiện:</b> <span>{props.noi_ph}</span></li>
                        <li><b>Ngày phát hiện:</b> <span>{props.ngay_ph}</span></li>
                        <li><b>Nơi làm việc:</b> <span>{props.noilamviec}</span></li>
                    </ul>
                    <ul className="list-actions list-inline mb-0 mt-1">
                        <li className="list-inline-item"><a href={link} target="_blank">Chi tiết</a></li>
                        <li className="list-inline-item"><a onClick={onKhoanhvung} target="_blank">Khoanh bùng</a></li>
                    </ul>
                </div>
            </div>

        </div>
    )
}

export default memo(Pop)