import React, { memo } from 'react'
import * as Space from 'react-spaces'
import { useSelector } from '@ttungbmt/redux-toolkit'
import { selectors as layout_sels, utils } from '@core/reducers/layout'
import { Slot } from "@ttungbmt/redux-layout"

function Footer() {
    const footerProps = useSelector(layout_sels.getFooter)

    return (
        <Space.BottomResizable as="footer" {...utils.pickSpace(footerProps)}>
            <Slot name="footer"/>
        </Space.BottomResizable>
    )
}

export default memo(Footer)