import { Model, attr, many } from 'redux-orm'
import slice from "../reducers/odich/odich.slice";

class Odich extends Model {
    toString() {
        return `Odich: ${this.name}`
    }
}

Odich.modelName = 'Odich'
Odich.reducers = slice.caseReducers

Odich.fields = {
    id: attr(),
    play: attr(),
    range: attr(),
    selected: attr({getDefault: () => false}),
    loading: attr({getDefault: () => false}),
    timer: attr({getDefault: () => 1000}),
    runStatus: attr({getDefault: () => 'Stopped'}),
    cabenhs: many('Cabenh', 'odichs')
}

export default Odich