import React, { memo } from 'react'
import { isEmpty } from 'lodash-es'
import cx from 'classnames'
import { Nav, Dropdown } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import { matchPath, useLocation } from '@ttungbmt/redux-toolkit'

const Item = memo(({text, icon}) => {
    return (
        <>{icon && <i className={cx(icon, 'mr-1')}/>} {text}</>
    )
})


function NavLink({url, items, onClick, ...props}) {
    const location = useLocation()
    const A = url ? Link : Nav.Link
    if (isEmpty(items)) return <A to={url} {...props} className={cx('nav-link', {active: matchPath(location.pathname, {path: url, exact: true})})}><Item {...props}/></A>

    return (
        <Nav.Item>
            <Dropdown as={Nav.Item}>
                <Dropdown.Toggle as={Nav.Link}><Item {...props}/></Dropdown.Toggle>
                <Dropdown.Menu>
                    {items.map(({onClick, ...i}, k) => <Dropdown.Item key={k}
                                                                      onClick={onClick}><Item {...i}/></Dropdown.Item>)}
                </Dropdown.Menu>
            </Dropdown>
        </Nav.Item>
    )
}

export default memo(NavLink)