import React, { memo, useState } from 'react'
import Box from '@material-ui/core/Box'
// import { castArray } from "lodash-es"
import * as Space from "react-spaces"
import { useSelector, useDispatch } from '@ttungbmt/redux-toolkit'
import { isSlot, filterSlot } from '@core/components/Slot'
import { selectors as layout_sels } from '@core/reducers/layout'
import ReactFullScreenElement from "react-fullscreen-element"

import Wrapper from './Wrapper'

function MapLayout({children, leftTool, rightTool, leaflet}) {
    const fullscreen = useSelector(layout_sels.getMapFullscreen)
    // const arrCh = castArray(children)
    //
    // const leaflet = filterSlot(arrCh, 'leaflet'),
    //     left_tool = filterSlot(arrCh, 'left_tool'),
    //     right_tool = filterSlot(arrCh, 'right_tool'),
    //     rest = arrCh.filter(o => !isSlot(o))

    return (
        <ReactFullScreenElement fullScreen={fullscreen}>
            <Wrapper>
                <Space.Top size={45} style={{borderBottom: '1px solid #dfdfdf', overflow: 'visible', zIndex: 999999}}>
                    <Box display="flex" height="100%">
                        <Box display="flex" flexGrow={1} alignItems="center" className="map-tools">{leftTool}</Box>
                        <Box className="map-tools map-rgt-tools"
                             css={{borderLeft: `1px solid rgba(223, 223, 223, 0.56)`}}>{rightTool}</Box>
                    </Box>
                </Space.Top>
                <Space.Fill>
                    {leaflet}
                </Space.Fill>
                <Space.Bottom size={0}>
                    Extra Map
                </Space.Bottom>

            </Wrapper>
        </ReactFullScreenElement>
    )
}

export default memo(MapLayout)