import React, { lazy } from 'react'
import {Redirect} from "react-router-dom";

const MapsContainer = lazy(() => import('./Container'))
const OdichPage = lazy(() => import('./views/odich/index'))
const OdichViewPage = lazy(() => import('./views/odich/view/index'))
const OdichListPage = lazy(() => import('./views/odich/list/index'))
const SxhPage = lazy(() => import('./views/ds-sxh/index'))
const HomePage = lazy(() => import('./views/home/index'))
const ThongkePage = lazy(() => import('./views/thongke/index'))
const DncPage = lazy(() => import('./views/pt-nguyco/index'))
const Cabenh = lazy(() => import('./views/cabenh/index'))

export const MapsConfig = {
    routes: [
        {
            path: '/maps',
            component: MapsContainer,
            routes: [
                {
                    path: '/maps/ds-sxh',
                    component: SxhPage
                },
                // {
                //     path: '/maps/cabenh',
                //     component: Cabenh
                // },
                {
                    path: '/maps/odich/view/:id',
                    component: OdichViewPage
                },
                {
                    path: '/maps/odich/list',
                    component: OdichListPage
                },
                {
                    path: '/maps/odich',
                    component: OdichPage
                },
                {
                    path: '/maps/pt-nguyco',
                    component: DncPage
                },
                {
                    path: '/maps/thongke',
                    component: ThongkePage
                },
                {
                    path: '/',
                    component: () => <Redirect to={'/maps/ds-sxh'}/>
                },
            ]
        },

    ]
}