import React, { memo, cloneElement } from 'react'
import { get } from '@ttungbmt/utils'

function PopManager({_key, model, features}) {
    const Component = get(features, `${_key}.pop`, () => <div>Không tìm thấy: <b>{_key}</b></div>)
    return <Component {...model}/>
}

export default memo(PopManager)