import React from 'react'
import { Redirect } from 'react-router-dom'
import { generateRoutesFromConfigs } from '@core/utils'

import { MapsConfig } from 'app/main/maps/config'

const routeConfigs = [
    MapsConfig
];

const routes = [
    ...generateRoutesFromConfigs(routeConfigs),
    {
        path: '/',
        exact: true,
        component: () => <Redirect to={'/maps'}/>
    },
    {
        component: () => <Redirect to="/pages/errors/error-404"/>
    },
];

export default routes