import React, { memo } from 'react'

function getLoaihinhText(props) {
    return `${props.nhom} - ${props.loaihinh}`
}

function Pop(props = {}) {
    let link = '/pt_nguyco/default/update?id=' + props.gid

    return (
        <div>
            <div className="pop-wrapper">
                <div className="pop-title text-uppercase">
                    <a href={link} target="_blank">
                        <div> {props.ten_cs}</div>
                    </a>
                </div>
                <div className="pop-body">
                    <ul className="list-data mb-10">
                        <li><b>Tên/ Người:</b> <span>{props.ten_cs}</span></li>
                        <li><b>Địa chỉ:</b> <span dangerouslySetInnerHTML={{__html: props.diachi}} /> - {props.tenquan} - {props.tenphuong}</li>
                        <li><b>Khu phố - Tổ dân phố:</b> <span>{props.khupho_to}</span></li>
                        <li><b>Nhóm - Loại hình:</b> <span>{getLoaihinhText(props)}</span></li>
                        <li><b>Ngày cập nhật:</b> <span>{props.ngaycapnhat}</span></li>
                    </ul>
                    <ul className="list-actions list-inline mb-0 mt-1">
                        <li className="list-inline-item"><a href={link} target="_blank">Chi tiết</a></li>
                    </ul>
                </div>
            </div>

        </div>
    )
}

export default memo(Pop)