import React, { memo, useEffect } from 'react'

import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import * as am4plugins_forceDirected from "@amcharts/amcharts4/plugins/forceDirected";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import AutoSizer from 'react-virtualized-auto-sizer'
am4core.useTheme(am4themes_animated);

function AmCharts({data}) {
    useEffect(() => {
        let chart = am4core.create('chartdiv', am4plugins_forceDirected.ForceDirectedTree)
        let series = chart.series.push(new am4plugins_forceDirected.ForceDirectedSeries())
        series.data = data;

        series.dataFields.value = "value";
        series.dataFields.name = "id";
        series.dataFields.children = "children";
        series.dataFields.color = "color";

        series.nodes.template.label.text = "{label}";
        series.nodes.template.label.truncate = true;

        series.fontSize = 13;
        series.maxRadius = am4core.percent(6);
        // series.minRadius = 30;
        series.maxRadius = 70;
        series.maxLevels = 1;
        series.nodes.template.tooltipText = "#{id}: {hoten}";
        // series.nodes.template.fillOpacity = 0.8;
        // series.events.on("hit", function(ev) {
        //     console.log(ev.target);
        // });

        series.nodes.template.events.on("up", function (event) {
            // const node = event.target;
            // node.expandAll = false;
        });

        let hoverState = series.links.template.states.create("hover");
        hoverState.properties.strokeWidth = 3;
        hoverState.properties.strokeOpacity = 1;

        series.nodes.template.events.on("over", function(event) {
            event.target.dataItem.childLinks.each(function(link) {
                link.isHover = true;
            })
            if (event.target.dataItem.parentLink) {
                event.target.dataItem.parentLink.isHover = true;
            }

        })

        series.nodes.template.events.on("out", function(event) {
            event.target.dataItem.childLinks.each(function(link) {
                link.isHover = false;
            })
            if (event.target.dataItem.parentLink) {
                event.target.dataItem.parentLink.isHover = false;
            }
        })


        // networkSeries.dataFields.linkWith = "linkWith";
        // series.dataFields.name = "name";
        // series.dataFields.id = "name";
        // series.dataFields.value = "value";
        // series.dataFields.children = "children";
        //
        // series.nodes.template.tooltipText = "{name}";
        // series.nodes.template.fillOpacity = 1;
        //
        // series.nodes.template.label.text = "{name}"
        // series.fontSize = 12;
        // networkSeries.maxLevels = 2;
        // networkSeries.maxRadius = am4core.percent(6);
        // networkSeries.manyBodyStrength = -16;
        // networkSeries.nodes.template.label.hideOversized = true;

        return () => {
            chart && chart.dispose()
        }
    }, [])

    return (
        <div id="chartdiv" style={{ width: "100%", minHeight: 600 }} />
    )
}
AmCharts.defaultProps = {
    data: []
}

export default memo(AmCharts)