import { createReducer } from "redux-orm";
import { includes, keys } from '@ttungbmt/utils'
import orm from './orm'
import { isEmpty } from '@ttungbmt/utils'

const ormReducer = createReducer(orm, defaultUpdater)

function defaultUpdater(session, action) {
    session.sessionBoundModels.forEach(modelClass => {
        const caseReducers = modelClass.reducers
        if (!isEmpty(caseReducers) && includes(keys(caseReducers), action.type)) {
            caseReducers[action.type](action, modelClass, session)
        }
    });
}

export default ormReducer