import Vuex from 'vuex'
import { get } from '@ttungbmt/utils'
import pathify from 'vuex-pathify'

const moduleMap = {
    state: {
        boundary: null,
    },
    mutations: {
        setMap(state, data){
            state.boundary = data.boundary
        }
    },
    actions: {

    },
}

const store = new Vuex.Store({
    state: {
        catalog: {}
    },
    getters: {
        getCat: state => (name) => {
            return get(state, `catalog.${name}`)
        }
    },
    mutations: {
        setCat(state, data) {
            state.catalog = data
        }
    },
    actions: {

    },
    modules: {
        map: moduleMap
    },
    plugins: [ pathify.plugin ],
})

export default store

export const getters = store.getters