import { createAction, createSelector } from '@ttungbmt/redux-toolkit'

import slice, {name} from './slice'

const initApp = createAction(`@${name}/INIT`)

const getApp = createSelector(['app'], r => r)
const getLoaded = createSelector([getApp], r => r.loaded)

export const actions = {
    initApp,
    ...slice.actions
}

export const selectors = {
    getApp,
    getLoaded,
}

export default slice.reducer