import React, { memo, Fragment  } from 'react'
import { useSelector } from '@ttungbmt/redux-toolkit'
import { isEmpty, omit, get, isUndefined } from '@ttungbmt/utils'
import { selectors as sels } from '../../index'

import { fnFormatCtrls } from "../../utils";
import { ctrlComps } from "../../fixtures/controls.fixture";

const Empty = memo(() => <></>)

function ControlsManager() {
    let ctrls = omit(useSelector(sels.getControls), ['layers'])
    if (isEmpty(ctrls)) return <Fragment/>

    return fnFormatCtrls(ctrls).filter(v => v.enabled === true || isUndefined(v.enabled)).map(({name, ...props}, k) => {
        let Component = get(ctrlComps, name, Empty)
        return <Component key={k} {...props}/>
    })
}

export default memo(ControlsManager)